import { connect } from "react-redux";
import { DEV_API_URL } from "../../../config/config";
import axios from "axios";
import OrderPage from "../components/OrderPage/OrderPage";
import { addToCart } from "../../../common/rootAction";

const mapStateToProps = state => ({
  cart: state["ROOT_REDUCER"].cart
});

const mapDispatchToProps = (dispatch, props) => ({
  createOrder: async cart => {
    let request = {
      cart: [],
      total_quantity: 0,
      total_discount: 0,
      total_price: 0
    };
    const data = cart.map((item, index) => {
      request.cart.push(item);
      request.total_quantity += item.quantity;
      request.total_price += item.quantity * item.item.p_price;
    });
    // console.log(request);
    let res = await axios.post(`${DEV_API_URL}/api/order`, request);
    dispatch(addToCart([]));
    alert(res.data);
  },

  removeCartItem: (cart, index) => {
    if (cart.length == 1) {
      return dispatch(addToCart([]));
    } else {
      let data = [];
      for (let i = 0; i < cart.length; i++) {
        if (i != index) {
          data.push(cart[i]);
        }
      }
      return dispatch(addToCart(data));
    }
  },

  changeQuantity: (cart, index, value) => {
    let data = cart.map((item, ind) => {
      if (index == ind) {
        return { ...item, ...{ quantity: parseInt(value) } };
      } else {
        return item;
      }
    });
    console.log(data);
    return dispatch(addToCart(data));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(OrderPage);
