import React, { Component } from 'react'
import Slider from "react-slick";
import cardImg from '../../../../media/images/HomePage/CardItem.png'
import Button from '../../../../common/components/Button/Button'
import './CardList.css'
import "./slick.css";
import Common from '../../../../common/common'
import goldMedal from '../../../../media/images/1st.png'
import silverMedal from '../../../../media/images/2nd.png'
import bronzeMedal from '../../../../media/images/3rd.png'

const settings = {
    infinite: true,
    speed: 200,
    slidesToShow: 6,
    slidesToScroll: 1,
    initialSlide: 0,
    responsive: [
        {
            breakpoint: 1200,
            settings: {
                slidesToShow: 4,
                slidesToScroll: 1,
                infinite: true
            }
        },
        {
            breakpoint: 992,
            settings: {
                slidesToShow: 3,
                slidesToScroll: 1,
                infinite: true
            }
        },
        {
            breakpoint: 768,
            settings: {
                slidesToShow: 2,
                slidesToScroll: 1,
                infinite: true
            }
        },
        {
            breakpoint: 576,
            settings: {
                slidesToShow: 1,
                slidesToScroll: 1,
                infinite: true
            }
        }
    ]
};

export default class CardList extends Component {
    constructor(props) {
        super(props)
        this.state = {
            // listCard: this.props.data.length <= this.props.length ? this.props.data : this.props.data.slice(0, this.props.length),
            listCard: this.props.data,
            begin: 0,
        }
    }

    componentWillMount = () => {
        // const { data, length } = this.props
        // console.log(data)
        // if (data.length <= length) {
        //     this.setState({ listCard: data })
        // } else {
        //     this.setState({ listCard: data.slice(0, length) })
        // }
    }

    renderMedal = (index) => {
        if (index === 0) {
            return <img className="medal" alt="1st" src={goldMedal}></img>
        }
        if (index === 1) {
            return <img className="medal" alt="2nd" src={silverMedal}></img>
        }
        if (index === 2) {
            return <img className="medal" alt="3rd" src={bronzeMedal}></img>
        }
    }

    renderItem = (item) => {
        return (
            <div className='card-item-container'>
                <img className='img-fluid' src={item.p_img[0] || cardImg} />
                <div className='overlay d-flex flex-column align-items-center justify-content-center'>
                    <p id="item-name">{item.p_name}</p>
                    <p id="item-price">Gi: {Common.formatCurrency(item.p_price)}</p>
                    <Button
                        text="Chi tit"
                        buttonStyle={{
                            backgroundColor: '#EF7F18',
                            width: '50%',
                            paddingTop: '5px',
                            paddingBottom: '5px',
                            borderRadius: '5px',
                        }}
                        onClick={() => { window.location.href = `/product/${item._id}` }}
                        transform={3}
                        shadowH={0}
                        shadowV={10}
                        blur={20}
                        animationDuration={0.1}
                    />
                </div>
            </div>
        )
    }

    renderItemResponsive = (item, index) => {
        let itemName = ""
        if(window.screen.width <= 1024) {
            itemName = item.p_name.split(" ").length <= 2 ? item.p_name.trim() : `${item.p_name.split(" ", 2).join(" ").trim()}...`
        } else {
            itemName = item.p_name.split(" ").length <= 3 ? item.p_name.trim() : `${item.p_name.split(" ", 3).join(" ").trim()}...`
        }
        return (
            <div className='card-item-container'>
                <div className="d-flex flex-column align-items-center justify-content-center">
                    { this.props.hasMedal && this.renderMedal(index) }
                    <img className='img-fluid' alt={item.p_name} src={item.p_img[0] || cardImg} />
                </div>
                <div className='overlay d-flex flex-column align-items-center justify-content-center'>
                    <p id="item-name">{itemName}</p>
                    <p id="item-price">Gi: {Common.formatCurrency(item.p_price)}</p>
                    <Button
                        text="Chi tit"
                        buttonStyle={{
                            backgroundColor: '#EF7F18',
                            width: '50%',
                            paddingTop: '5px',
                            paddingBottom: '5px',
                            borderRadius: '5px',
                        }}
                        onClick={() => { window.location.href = `/product/${item._id}` }}
                        transform={3}
                        shadowH={0}
                        shadowV={10}
                        blur={20}
                        animationDuration={0.1}
                    />
                </div>
            </div>
        )
    }

    renderList() {
        const { listCard } = this.state
        if (listCard) {
            return (
                <div className='card-list d-flex flex-row justify-content-between'>
                    {
                        listCard.map((item) => {
                            return { ...this.renderItem(item) }
                        })
                    }
                </div>
            )
        } else {
            return (
                <div>
                    <p>Cha c sn phm no</p>
                </div>
            )
        }
    }

    renderListResponsive() {
        const { listCard } = this.state
        if (listCard) {
            return (
                listCard.map((item, index) => {
                    return { ...this.renderItemResponsive(item, index) }
                })
            )
        } else {
            return (
                <div>
                    <p>Cha c sn phm no</p>
                </div>
            )
        }
    }

    moveLeft = () => {
        const { listCard, begin } = this.state
        const { data, length } = this.props
        if (begin == 0) {
            // Do nothing
        } else {
            this.setState({ listCard: data.slice(begin - 1, begin + length - 1), begin: begin - 1 })
        }
    }

    moveRight = () => {
        const { listCard, begin } = this.state
        const { data, length } = this.props
        if (begin == data.length - length) {
            // Do nothing
        } else {
            this.setState({ listCard: data.slice(begin + 1, begin + length + 1), begin: begin + 1 })
        }
    }

    render() {
        return (
            <Slider className="card-list" {...settings}>
                {
                    this.renderListResponsive()
                }
            </Slider>
        )
    }
}