import React, { Component } from "react";
import Header from "../../common/containers/Header";
import BestSeller from "./components/BestSeller/BestSeller";
import HitCards from "./components/HitCards/HitCards";
//import '../../common/styles/LoginPage.css';
import "../../common/styles/HomePage.css";
import GoToStore from "./components/GoToStore/GoToStore";
import Loading from '../../common/components/Loading/Loading'
import { connect } from 'react-redux'
import { MODEL_NAME } from './model'
import axios from 'axios'
import { DEV_API_URL } from '../../config/config'
import { setHitCards, setBestSeller } from './action'
import { toggleLoading } from "../../common/rootAction";
// import Footer from "./containers/Footer";

const mapStateToProps = (state) => ({
  loading: state['ROOT_REDUCER'].loading,
  bestSeller: state[MODEL_NAME].bestSeller,
  hitCards: state[MODEL_NAME].hitCards,
})

const mapDispatchToProps = (dispatch, props) => ({
  getData: async () => {
    dispatch(toggleLoading(true))
		let resBestSeller = await axios.get(`${DEV_API_URL}/api/product/get/bestseller`)
    dispatch(setBestSeller(resBestSeller.data))
    let resHitCards = await axios.get(`${DEV_API_URL}/api/product/get/hitcards`)
    dispatch(setHitCards(resHitCards.data))
    dispatch(toggleLoading(false))
	}
})

class HomePage extends Component {
  componentDidMount() {
    this.props.getData()
  }

  render() {
    return (
      <div className="home-page">
        <Header hasImg="true" content="true" height="100vh" />
        <Loading visible={this.props.loading}/>
        <BestSeller bestSeller={this.props.bestSeller}/>
        <HitCards hitCards={this.props.hitCards}/>
        <GoToStore />
      </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(HomePage)