import React, { Component } from 'react'
import cardImg from '../../../../media/images/HomePage/CardItem.png'
import ReactPaginate from 'react-paginate'
import Filter from '../../containers/Filter'
import './ProductList.css'
import Common from '../../../../common/common'
import Loading from '../../../../common/components/Loading/Loading';
import CartAlert from '../../../../common/components/CartAlert/CartAlert';

export default class ProductList extends Component {
    constructor(props) {
        super(props)
        this.state = {
            c_name: "",
            l_name: "",
            s_name: "",
            page: 1,
            cartAlert: [],
        }
    }

    componentDidMount() {
        this.props.getData("","","",1)
    }

    popCartAlert = () => {
        let newCartAlert = this.state.cartAlert
        if(newCartAlert && newCartAlert.length !== 0) {
            newCartAlert.splice(0, 1)
        }
        this.setState({cartAlert: newCartAlert})
    }

    onFilter = (value, type) => {
        switch(type) {
            case 0:
                {
                    this.setState({c_name: value, l_name: "", s_name: "", page: 1})
                    this.props.getData(value, "", "", 1)
                    break
                }
            case 1:
                {
                    this.setState({c_name: "", l_name: value, s_name: "", page: 1})
                    this.props.getData("", value, "", 1)
                    break
                }
            case 2:
                {
                    this.setState({c_name: "", l_name: "", s_name: value, page: 1})
                    this.props.getData("", "", value, 1)
                    break
                }
            case 3:
                {
                    this.setState({c_name: "", l_name: "", s_name: "", page: 1})
                    this.props.getData("", "", "", 1)
                    break
                }
        }
    }

    onChangePage = (page) => {
        const { c_name, l_name, s_name } = this.state
        this.props.getData(c_name, l_name, s_name, page)
    }

    onAddToCart = (item) => {
        const { cart } = this.props
        const newCartAlert = {
            id: item._id,
            name: item.p_name,
            quantity: 1,
        }
        this.props.addToCart(cart, item)
        this.setState(state => ({
            cartAlert: [...state.cartAlert, newCartAlert]
        }))
    }

    renderItem = (item) => {
        let itemName = ""
        if(window.screen.width <= 1024) {
            itemName = item.p_name.split(" ").length <= 2 ? item.p_name.trim() : `${item.p_name.split(" ", 2).join(" ").trim()}...`
        } else {
            itemName = item.p_name.split(" ").length <= 3 ? item.p_name.trim() : `${item.p_name.split(" ", 3).join(" ").trim()}...`
        }
        return (
            <div className="product-item d-flex flex-column">
                <img
                    className="img-thumbnail img-fluid"
                    src={item.p_img ? item.p_img[0] : cardImg}
                    style={{ cursor: 'pointer' }}
                    onClick={() => { window.location.href = `/product/${item._id}` }}
                    alt={item.p_name}
                />
                <div className="product-item-info d-flex flex-column">
                    <p id="name" onClick={() => { window.location.href = `/product/${item._id}` }}>{itemName}</p>
                    <p id="price" onClick={() => { window.location.href = `/product/${item._id}` }}>{Common.formatCurrency(item.p_price)}</p>
                    <button type="button" className="add-to-cart btn btn-info align-self-center" onClick={() => this.onAddToCart(item)}>Thm vo gi</button>
                </div>
            </div>
        )
    }

    render() {
        const { products } = this.props
        const { cartAlert } = this.state
        if(products.data) {
            return (
                <div className="product-list-page row">
                    <CartAlert data={cartAlert} popCartAlert={this.popCartAlert}/>
                    <Filter onFilter={this.onFilter}/>
                    <div className="d-flex flex-column col-sm-9" style={{padding: 0}}>
                        <div className="product-list d-flex flex-wrap flex-row">
                            {
                                products.data.length != 0 ? products.data.map((item) => {
                                    return { ...this.renderItem(item) }
                                })
                                :
                                <h5 style={{color: 'white'}}>Khng tm thy kt qu</h5>
                            }
                        </div>
                        {
                            products.data.length != 0 &&
                            <ReactPaginate 
                                containerClassName="pagination container-fluid d-flex justify-content-center"
                                pageRangeDisplayed={1}
                                pageCount={products.num_pages}
                                previousLabel={"<"}
                                nextLabel={">"}
                                breakLabel={"..."}
                                breakClassName="next"
                                breakLinkClassName="next-link"
                                pageClassName="pagination-item"
                                pageLinkClassName="pagination-link d-flex align-items-center justify-content-center"
                                activeLinkClassName="active-page-item"
                                previousClassName="prev"
                                previousLinkClassName="prev-link d-flex align-items-center justify-content-center"
                                nextClassName="next"
                                nextLinkClassName="next-link d-flex align-items-center justify-content-center"
                                onPageChange={(page)=>this.onChangePage(page.selected+1)}
                            />
                        }
                    </div>
                </div>
            )
        } else {
            return (
                <div className="d-flex flex-row">
                    <Filter onFilter={this.onFilter}/>
                    <div className="d-flex flex-column align-items-center col-sm-9">
                        <h5 style={{color: 'white'}}>Khng tm thy kt qu</h5>
                    </div>
                </div>
            )
        }
    }
}