import React, { Component } from 'react';
import './CartAlert.css';

class ItemAlert extends Component {
  constructor(props) {
    super(props)
  }

  componentDidMount() {
    window.setTimeout(() => {
      this.props.popCartAlert();
    }, 1800)
  }

  render() {
    const { item } = this.props
    return (
      <div className="item-alert alert alert-success fade show">
        <strong>{item.name} ({item.quantity})</strong> c thm vo gi hng
      </div>
    )
  }
}

export default class CartAlert extends Component {
  constructor(props) {
    super(props)
  }

  render() {
    const { data } = this.props
    return (
      <div className="cart-alert">
        {
          data.map((item, index) => {
            return <ItemAlert item={item} index={index} popCartAlert={this.props.popCartAlert}/>
          })
        }
      </div>
    )
  }
}