import React, { Component } from 'react'
import './HitCards.css'
import CardList from '../CardList/CardList'

export default class HitCards extends Component {
  render () {
    if(this.props.hitCards.length != 0) {
      return (
        <div className="container-fluid hit-cards d-flex flex-column align-items-center">
          <p className="hit-cards-title">Hit cards</p>
          <p className="in-this-weak-title">in this week</p>
          <CardList data={this.props.hitCards} length={6}/>
          <hr/>
        </div>
      )
    } else {
      return (
        <div></div>
      )
    }
  }
}