import React, { Component } from "react";
import "./OrderPage.css";
import cardImg from "../../../../media/images/HomePage/CardItem.png";
import Common from "../../../../common/common";
import Delivery from "../../../DeliveryPage/index";
const shipping_fee = 20000;

export default class OrderPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        {
          _id: "",
          p_name: "Modric1",
          p_price: 10000,
          p_stock: 12,
          p_desciption: "String test",
          // p_img: [],
          quantity: 1,
          c_name: "SOCEART",
          league: "",
          season: "2018-2019"
        },
        {
          _id: "",
          p_name: "Modric2",
          p_price: 20000,
          p_stock: 12,
          p_desciption: "String test2",
          //p_img: [],
          quantity: 1,
          c_name: "Panini",
          league: "",
          season: "2018-2019"
        }
      ],
      total_quantity: 0,
      total_discount: 0,
      total_price: 30000,
      status: "Pending",

      deliveryCheck: false
    };
  }

  //Handle change quatity from CartList.js
  changeQuantity = (event, index) => {
    this.props.changeQuantity(this.props.cart, index, event.target.value || 1);
  };

  iconQuantityHandler = (index, value) => {
    if (value <= 1) {
      value = 1;
    }
    this.props.changeQuantity(this.props.cart, index, value);
  };

  //Calculate total price
  calculateTotal() {
    let total_price = 0;
    const data = this.props.cart.map((item, index) => {
      // cart.push(item);
      //total_quantity += item.quantity;
      total_price += item.quantity * item.item.p_price;
    });

    return total_price;
  }

  componentDidMount() {
    // console.log(this.props.cart);
  }
  //Change deliveryCheck

  changeDeliveryCheck() {
    this.setState({
      deliveryCheck: !this.state.deliveryCheck
    });
  }
  //render item infomation
  render_item(index, item, quantity) {
    //console.log("SL:" + quantity);
    return (
      <div className="order-item-container ">
        <div className="d-flex flex-row justify-content-center align-self-center">
          <div className="row">
            <img
              className="img-fluid img-thumnail col-3  justify-content-center align-self-center"
              alt="Img"
              src={item.p_img ? item.p_img[0] : cardImg}
            />

            <div className="col-9 d-flex flex-column align-items-around justify-content-around">
              <div>
                <h1 className="product-title-secondary-txt ">{item.p_name}</h1>
                <h2 className="product-info-txt">by {item.c_name}</h2>
              </div>

              <div className="d-flex flex-row justify-content-around">
                <h3
                  className="link price-small-txt"
                  onClick={() => {
                    // alert("delete");

                    this.props.removeCartItem(this.props.cart, index);
                  }}
                >
                  Xa
                </h3>
                <h3 className="price-small-txt">
                  {Common.formatCurrency(item.p_price)}
                </h3>

                <div className="d-flex price-small-txt flex-row align-items-center">
                  <input
                    value={quantity}
                    className="quantity"
                    size={4}
                    onChange={event => {
                      this.changeQuantity(event, index);
                    }}
                  />
                  <div className="quantity-icons d-flex flex-column">
                    <i
                      className="change-quantity-icon fa fa-angle-up"
                      onClick={() => {
                        this.iconQuantityHandler(index, quantity + 1);
                      }}
                    />
                    <i
                      className="change-quantity-icon fa fa-angle-down"
                      onClick={() => {
                        this.iconQuantityHandler(index, quantity - 1);
                      }}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  //render cash infomation
  render_cash_info() {
    return (
      <div className="col-md-4 col-sm-12 ml-auto d-flex flex-column">
        <div className="cash-info">
          <div className="d-flex flex-column ">
            <div className="d-flex flex-row justify-content-between align-text-bottom">
              <h3 className="price-small-txt">Tm tnh: </h3>
              <h3 className="price-small-txt">
                {Common.formatCurrency(this.calculateTotal())}
              </h3>
            </div>

            {/*<h3 className="product-info-txt">
  Ph vn chuyn:{" "}
  <h3 className="price-txt">
    {Common.formatCurrency(shipping_fee)}
  </h3>
    </h3>*/}
            <div className="d-flex flex-row justify-content-between align-text-bottom">
              <h3 className="price-small-txt">Thnh tin: </h3>
              <h3 className="price-small-txt">
                {/*Common.formatCurrency(this.state.total_price + shipping_fee)*/}
                {Common.formatCurrency(this.calculateTotal())}
              </h3>
            </div>
            <hr />
          </div>

          <div className="">
            Hnh thc thanh ton:
            <br />
            <ul>
              <li>Thanh ton tin mt khi nhn hng (COD).</li>

              <li>
                Ph vn chuyn s c chng ti thng bo sau khi xc nhn n
                hng.
              </li>
            </ul>
          </div>
        </div>

        <div
          className="btn order-btn product-info-txt"
          onClick={() => {
            // this.props.createOrder(this.props.cart);
            // window.location.href = "/delivery";
            this.changeDeliveryCheck();
          }}
        >
          Tip tc
        </div>
      </div>
    );
  }

  //render cart info
  render_cart_info() {
    // const items = this.state.items;
    const cart = this.props.cart;
    //console.log(this.props.cart);
    const listItems = cart.map((cart_item, index) =>
      this.render_item(index, cart_item.item, cart_item.quantity)
    );
    return (
      <div className="order-info-container">
        {cart.length ? (
          <div className="container-fluid">
            <div className="row">
              <div className="col-md-6 offset-md-1 col-sm-12">
                <h3 className="" style={{ color: "white" }}>Thng tin n hng</h3>
                {listItems}
              </div>
              {this.render_cash_info()}
            </div>
          </div>
        ) : (
            <div className="empty-cart text-center">
              <div className="  product-title-secondary-txt ">
                Khng c sn phm no trong gi hng ca bn
            </div>
              <div
                className="product-info-txt link"
                onClick={() => {
                  window.location.href = "/products";
                }}
              >
                Nhn vo y  tip tc mua hng
            </div>
            </div>
          )}
      </div>
    );
  }

  //render delivery component
  render_delivery() {
    return <Delivery />;
  }

  render() {
    return (
      <div className="order-page-container">
        {this.state.deliveryCheck
          ? this.render_delivery()
          : this.render_cart_info()}
      </div>
    );
  }
}
