import { connect } from "react-redux";
import { MODEL_NAME } from "../model";
import { setRelatedProductData, setProductInfo } from "../action";
import { addToCart, toggleLoading } from "../../../common/rootAction";
import ProductInfo from "../components/ProductInfo/ProductInfo";
import axios from "axios";
import { DEV_API_URL } from "../../../config/config";

const mapStateToProps = state => ({
  relatedProducts: state[MODEL_NAME].relatedProducts,
  productInfoData: state[MODEL_NAME].productInfoData,
  cart: state["ROOT_REDUCER"].cart
});

const mapDispatchToProps = (dispatch, props) => ({
  getData: async (_id) => {
    dispatch(toggleLoading(true))
    let resBestSeller = await axios.get(`${DEV_API_URL}/api/product/get/bestseller`);
    dispatch(setRelatedProductData(resBestSeller.data));
    let res = await axios.get(DEV_API_URL + "/api/product/get?_id=" + _id);
    dispatch(setProductInfo(res.data));
    dispatch(toggleLoading(false))
  },

  addToCartWithQuantity: (cart, item, quantity) => {
    if (cart.length == 0) {
      // Add First Item Into Cart
      return dispatch(
        addToCart([
          {
            item,
            quantity: quantity
          }
        ])
      );
    } else {
      // Flag newData To Check If item Is New In Cart Or Not
      let newData = true;
      let newCart = cart.map((value, index) => {
        if (item._id == value.item._id) {
          // If item Exists In Cart Then Its Quantity Plus 1
          newData = false;
          return {
            item: value.item,
            quantity: parseInt(value.quantity) + parseInt(quantity)
          };
        } else {
          // Else Return Others Items In Cart
          return value;
        }
      });
      if (newData) {
        // If item Is New Add New Item In newCart
        newCart = newCart.concat([
          {
            item: item,
            quantity: quantity
          }
        ]);
      }
      return dispatch(addToCart(newCart));
    }
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ProductInfo);
