import React, { Component } from "react";
import CardImg from "../../../media/images/HomePage/CardItem.png";
import "./CartList.css";
import Common from "../../common";

export default class CartList extends Component {
  changeQuantity = (event, index) => {
    this.props.changeQuantity(this.props.cart, index, event.target.value || 1);
  };

  iconQuantityHandler = (index, value) => {
    if (value <= 1) {
      value = 1;
    }
    this.props.changeQuantity(this.props.cart, index, value);
  };

  renderItem = (item, index) => {
    const { cart } = this.props;
    return (
      <div className="cart-item d-flex flex-row">
        <i
          className="remove-item-icon fas fa-times"
          onClick={() => {
            this.props.removeCartItem(cart, index);
          }}
        />
        <div className="cart-item-img-container">
          <img alt={item.item.p_name}
            className="img-thumbnail cart-item-img"
            src={item.item.p_img ? item.item.p_img[0] : CardImg}
          />
        </div>
        <div className="cart-item-info d-flex flex-column justify-content-around">
          <p>{item.item.p_name}</p>
          <p>Gi: {Common.formatCurrency(item.item.p_price)}</p>
          <div className="d-flex flex-row align-items-center">
            <p>SL: </p>
            <input
              value={item.quantity}
              className="quantity"
              size={4}
              onChange={event => {
                this.changeQuantity(event, index);
              }}
            />
            <div className="quantity-icons d-flex flex-column">
              <i
                className="change-quantity-icon fa fa-angle-up"
                onClick={() => {
                  this.iconQuantityHandler(index, item.quantity + 1);
                }}
              />
              <i
                className="change-quantity-icon fa fa-angle-down"
                onClick={() => {
                  this.iconQuantityHandler(index, item.quantity - 1);
                }}
              />
            </div>
          </div>
        </div>
      </div>
    );
  };

  render() {
    const { cart } = this.props;
    if (cart.length != 0) {
      return (
        <div className="cart-list d-flex flex-column">
          {cart.map((item, index) => {
            return { ...this.renderItem(item, index) };
          })}
          <div className="button-container d-flex flex-row justify-content-around align-self-center">
            <button
              className="btn btn-success"
              style={{ backgroundColor: "#1f78d1" }}
              onClick={() => {
                window.location.href = "/checkout";
              }}
            >
              Thanh ton
            </button>
            {/*<button className="btn btn-warning" onClick={this.props.clearCart}>
          Xo ht
        </button>*/}
          </div>
        </div>
      );
    } else {
      return (
        <div className="no-data d-flex justify-content-center">
          <p>Khng c sn phm no trong gi hng ca bn.</p>
        </div>
      );
    }
  }
}
