import React, { Component } from 'react'
import CardList from '../CardList/CardList'
import './BestSeller.css'

export default class BestSeller extends Component {
  render () {
    if(this.props.bestSeller.length != 0) {
      return (
        <div className="container-fluid best-seller d-flex flex-column">
          <p className="best-seller-title text-center">Best seller</p>
          <p className="of-all-time-title text-center">of all time</p>
          <CardList data={this.props.bestSeller} length={6} hasMedal={true} />
          <hr/>
        </div>
      )
    } else {
      return (
        <div></div>
      )
    }
  }
}