import { connect } from 'react-redux'
import CartList from '../components/CartList/CartList'
import { addToCart } from '../rootAction'

const mapStateToProps = (state) => ({
  cart: state['ROOT_REDUCER'].cart
})

const mapDispatchToProps = (dispatch, props) => ({
  clearCart: () => {
    return dispatch(addToCart([]))
  },
  
  changeQuantity: (cart, index, value) => {
    let data = cart.map((item, ind) => {
      if(index == ind) {
        return {...item,...{ quantity: parseInt(value) }}
      } else {
        return item
      }
    })
    console.log(data)
    return dispatch(addToCart(data))
  },

  removeCartItem: (cart, index) => {
    if(cart.length == 1) {
      return dispatch(addToCart([]))
    } else {
      let data = []
      for(let i=0; i<cart.length; i++) {
        if(i != index) {
          data.push(cart[i])
        }
      }
      return dispatch(addToCart(data))
    }
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(CartList)