import { handleActions } from 'redux-actions'
import * as actions from './action'

const defaultState = {
	bestSeller: [],
	hitCards: [],
}

const handlers = {
	[actions.setBestSeller]: (state, action) => ({
		...state,
		...{ bestSeller: action.payload }
	}),
	[actions.setHitCards]: (state, action) => ({
		...state,
		...{ hitCards: action.payload}
	})
}

export default handleActions(handlers, defaultState)