import React, { Component } from "react";
import "./ProductInfo.css";
import CardList from "../../../HomePage/components/CardList/CardList";
import cardImg from "../../../../media/images/HomePage/CardItem.png";
import Common from "../../../../common/common";
import Button from "../../../../common/components/Button/Button";
import CartAlert from '../../../../common/components/CartAlert/CartAlert';

export default class ProductInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      quantity: 1,
      p_name: "Modric",
      p_price: 20000,
      p_stock: 12,
      p_desciption: "String test",
      p_img: [],
      sold_quantity: 1,
      c_name: "SOCEART",
      league: "",
      season: "2018-2019",
      cartAlert: [],
    };
  }
  changeQuantity = event => {
    this.setState({ quantity: event.target.value });
  };

  iconQuantityHandler = value => {
    if (value <= 0) {
      value = 0;
    }
    this.props.changeQuantity(this.props.cart, value);
  };

  componentDidMount = () => {
    this.props.getData(this.props.p_id);
  };

  popCartAlert = () => {
    let newCartAlert = this.state.cartAlert
    if(newCartAlert && newCartAlert.length !== 0) {
      newCartAlert.splice(0, 1)
      this.setState({cartAlert: newCartAlert})
    }
  }

  onAddToCart = () => {
    const { cart, productInfoData } = this.props
    const { quantity } = this.state
    const newCartAlert = {
        id: productInfoData._id,
        name: productInfoData.p_name,
        quantity: quantity,
    }
    this.props.addToCartWithQuantity(cart, productInfoData, quantity)
    this.setState(state => ({
        cartAlert: [...state.cartAlert, newCartAlert]
    }))
  }

  render() {
    const { cartAlert } = this.state
    return (
      <div className="container-fluid product-info-container" style={{ paddingTop: "2vw" }}>
        <CartAlert data={cartAlert} popCartAlert={this.popCartAlert}/>
        <div className="row container-fluid d-flex justify-content-between">
          <img
            className="img-fluid img-thumnail card-info-img col-md-3 col-sm-12 "
            alt={this.props.productInfoData.p_name}
            src={
              this.props.productInfoData.p_img
                ? this.props.productInfoData.p_img[0]
                : cardImg
            }
          />

          <div className="card-details-info col-md-8 col-sm-12 ">
            <div>
              <h1 className="product-title-txt">
                {this.props.productInfoData.p_name}
              </h1>

              <h4 className="product-category-txt">
                {this.props.productInfoData.c_name +
                  " Card " +
                  this.props.productInfoData.l_name +
                  " " +
                  this.props.productInfoData.s_name}
              </h4>

              <hr />
          
              <p className="product-info-txt">
                Gi: {Common.formatCurrency(this.props.productInfoData.p_price)}
              </p>

              <p className="product-info-txt">
                S lng:{" "}
                <input
                  size={4}
                  onChange={event => {
                    this.changeQuantity(event);
                  }}
                  type="number"
                  name="quantity"
                  step="1"
                  min="1"
                  placeholder="1"
                />
              </p>
            </div>
            {/* <button
                type="button"
                class="btn-info btn-lg"
                style={{ backgroundColor: "#47B35D" }}
                onClick={() =>
                  this.props.addToCartWithQuantity(
                    this.props.cart,
                    this.props.productInfoData,
                    this.state.quantity
                  )
                }
              >
                Thm vo gi
              </button>*/}
            <div className="row container-fluid">
              <div className="product-info-txt add-to-cart-btn">
                <Button
                  text="Thm vo gi"
                  onClick={this.onAddToCart}
                  buttonStyle={{
                    //alignSelf: 'center',
                    width: "100%",

                    backgroundColor: "#1f78d1",
                    // paddingTop: "30px",
                    // paddingBottom: "30px",
                    borderRadius: "15px",
                    marginTop: "2vw"
                  }}
                  textStyle={{
                    color: "white",
                    // fontSize: "4vw",
                    fontWeight: "bold"
                  }}
                  transform={1} //transform translateY negative in px
                  shadowH={0} //horizontal shadow in px
                  shadowV={1} //vertical shadow in px
                  blur={10} //blur in px
                  animationDuration={0.2} //animation duration in seconds
                />
              </div>
            </div>

            {/* <p className="row product-info-txt">
              Available: {this.props.productInfoData.p_stock}
              </p> */}
          </div>
        </div>

        <div className="row container-fluid">
          <div className="bottom-container col-12 ">
            <h2 className="product-title-secondary-txt">Chi tit</h2>
            <h5 className="product-info-txt">
              {this.props.productInfoData.p_desciption}
            </h5>
          </div>
        </div>

        {this.props.relatedProducts.length ? (
          <div className="row container-fluid">
            <div className="related-products-container d-flex flex-column align-items-center">
              <h2 className="related-products-title-txt">Sn phm tng t</h2>
              <div className="container-fluid">
                <CardList data={this.props.relatedProducts} />
              </div>
            </div>
          </div>
        ) : (
          <div />
        )}
      </div>
    );
  }
}
