import React, { Component } from 'react'

export default class Button extends Component {
  constructor(props) {
    super(props)
    this.state = {
      hover: false,
    }
  }

  render() {
    const { onClick, buttonStyle, text, textStyle, transform, shadowH, shadowV, blur, animationDuration, icon} = this.props
    return (
      <div 
        className="d-flex justify-content-center"
        onMouseOver={()=>{this.setState({hover: true})}}
        onMouseLeave={()=>{this.setState({hover: false})}}
        onClick={onClick} 
        style={
          this.state.hover && 
          {transform: `translateY(-${transform}px)`, boxShadow: `${shadowH}px ${shadowV}px ${blur}px`, cursor: 'pointer', transitionDuration: `${animationDuration}s`, ...buttonStyle} 
          || 
          {cursor: 'pointer', transitionDuration: `${animationDuration}s`, ...buttonStyle}}
      > 
        <p style={{margin: 0, ...textStyle}}><i className={icon}></i> {text}</p>
      </div>
    )
  }
}