import { connect } from "react-redux";
import { MODEL_NAME } from "../model";
import {
  setCityList,
  setDistrictList,
  setWardList,
  addToCart
} from "../action";
import DeliveryForm from "../components/DeliveryForm/DeliveryForm";
import axios from "axios";
import { DEV_API_URL } from "../../../config/config";
import { toggleLoading } from "../../../common/rootAction";

const mapStateToProps = state => ({
  cityList: state[MODEL_NAME].cityList,
  districtList: state[MODEL_NAME].districtList,
  wardList: state[MODEL_NAME].wardList,
  cart: state["ROOT_REDUCER"].cart
});

function generateOptions(data) {
  return data.map(e => ({
    label: e.name,
    value: e.id
  }));
}

const mapDispatchToProps = (dispatch, props) => ({
  getCityList: async () => {
    const res = await axios.get(`${DEV_API_URL}/api/delivery/city`);
    return dispatch(setCityList(generateOptions(res.data)));
  },
  getDistrictList: async cityId => {
    const res = await axios.get(
      `${DEV_API_URL}/api/delivery/city/${cityId}/district`
    );
    return dispatch(setDistrictList(generateOptions(res.data)));
  },
  getWardList: async (cityId, districtId) => {
    const res = await axios.get(
      `${DEV_API_URL}/api/delivery/city/${cityId}/district/${districtId}`
    );
    return dispatch(setWardList(generateOptions(res.data)));
  },
  createOrder: async (cart, state) => {
    let request = {
      u_name: state.u_name.value,
      phone: state.phone.value,
      email: state.email.value,
      fbLink: state.fbLink.value,
      address: {
        city: state.address.city.value,
        district: state.address.district.value,
        ward: state.address.ward.value,
        street: state.address.street.value
      },
      cart: [],
      total_quantity: 0,
      total_discount: 0,
      total_price: 0
    };
    cart.forEach(item => {
      request.cart.push(item);
      request.total_quantity += item.quantity;
      request.total_price += item.quantity * item.item.p_price;
    });
    dispatch(toggleLoading(true));
    await axios.post(`${DEV_API_URL}/api/order`, request);
    dispatch(toggleLoading(false));
    dispatch(addToCart([]));
    alert(
      "n hng  c ghi nhn. Chng ti s lin lc vi bn trong thi gian sm nht."
    );
    window.location.href = "/";
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DeliveryForm);
