import React, { Component } from 'react'
import './Filter.css'

export default class Filter extends Component {
  constructor(props) {
    super(props)
    this.state = {
      categories: false,
      leagues: false,
      seasons: false,
    }
  }

  componentDidMount() {
    this.props.getData()
  }

  toggleCategories = () => {
    const { categories } = this.state
    this.setState({ categories: !categories })
  }

  toggleLeagues = () => {
    const { leagues } = this.state
    this.setState({ leagues: !leagues })
  }

  toggleSeasons = () => {
    const { seasons } = this.state
    this.setState({ seasons: !seasons })
  }

  renderSubFilter = (data, type) => {
    console.log(data)
    return (
      <div className="sub-filter d-inline-flex flex-column">
        {
          data.map((item)=>{
            return(
              <div className="sub-filter-item d-inline-flex flex-row align-items-center">
                <i className="fas fa-angle-right"></i>
                <h5 onClick={()=>{this.props.onFilter(item.c_name || item.l_name || item.s_name, type)}}>{item.c_name}{item.l_name}{item.s_name}</h5>
              </div>
            )
          })
        }
      </div>
    )
  }

  render() {
    const { categories, leagues, seasons } = this.state
    return (
      <div className="filter col-sm-3">
        <h3>Hin th theo</h3>
        <hr />
        <div className="d-inline-flex flex-column">
          <div className="filter-item d-inline-flex flex-row align-items-center" onClick={this.toggleCategories}>
            <i className="fas fa-caret-right"></i>
            <h5>Loi</h5>
          </div>
          {categories && this.props.categories.categories && this.renderSubFilter(this.props.categories.categories, 0)}
          <div className="filter-item d-inline-flex flex-row align-items-center" onClick={this.toggleLeagues}>
            <i className="fas fa-caret-right"></i>
            <h5>Gii u</h5>
          </div>
          {leagues && this.props.categories.leagues && this.renderSubFilter(this.props.categories.leagues, 1)}
          <div className="filter-item d-inline-flex flex-row align-items-center" onClick={this.toggleSeasons}>
            <i className="fas fa-caret-right"></i>
            <h5>Ma gii</h5>
          </div>
          {seasons && this.props.categories.seasons && this.renderSubFilter(this.props.categories.seasons, 2)}
          <div className="filter-item d-inline-flex flex-row align-items-center">
            <i className="fas fa-caret-right"></i>
            <h5 onClick={()=>{this.props.onFilter("", 3)}}>Tt c</h5>
          </div>
        </div>
      </div>
    )
  }
}