import React, { Component } from "react";
import validator from 'validator';
import "./Tracking.css";
import Button from "../../../../common/components/Button/Button";
import OrderStatus from "../OrderStatus/OrderStatus"

export default class Tracking extends Component {
  constructor(props) {
    super(props);
    this.state = {
      orderId: {
        valid: true,
        required: true,
        invalidText: 'M n hng khng hp l.',
        requiredTxt: 'Vui lng nhp m n hng.'
      },
      phone: {
        valid: true,
        required: true,
        invalidText: 'S in thoi khng hp l.',
        requiredTxt: 'Vui lng nhp s in thoi.'
      }
    };
  }

  componentDidMount() {
    this.props.setOrderId('')
    this.props.setPhone('')
    this.props.setOrder({})
  }

  validateOrderId = (e) => {
    const orderId = this.props.orderId
    this.setState({ orderId: { ...this.state.orderId, valid: validator.matches(orderId, /#?\d{4,}$/) } });
  }

  validatePhoneNumber = (e) => {
    const phone = this.props.phone
    this.setState({ phone: { ...this.state.phone, valid: validator.isMobilePhone(phone, 'vi-VN') } });
  }

  validateTrackingForm = async () => {
    await this.validateOrderId()
    await this.validatePhoneNumber()
    if (this.state.orderId.valid && this.state.phone.valid) {
      this.props.getOrder(this.props.orderId, this.props.phone).then(() => {
      })
      return true
    }
    return false
  }

  renderRequiredStart() {
    return <span style={{ color: 'red' }}>*</span>
  }

  render() {
    return (
      <div className="container-fluid tracking-page-container">
        <div className="col-md-10 offset-md-1">
          <h3 className="row page-title">Tra cu n hng</h3>
          <div className="row">
            <form id="tracking-form" className="col-md-5">
              <div className="form-group">
                <label for="o_id">M n hng{this.renderRequiredStart()}:</label>
                <input required value={this.props.orderId} onChange={e => this.props.setOrderId(e.target.value)} type="text" className={"form-control " + (this.state.orderId.valid ? '' : "is-invalid")} id="o_id" placeholder="S n hng" onBlur={this.validateOrderId} />
                <div className={"invalid-feedback " + (this.state.orderId.valid ? '' : 'd-block')} >
                  {
                    validator.isEmpty(this.props.orderId) ? this.state.orderId.requiredTxt : this.state.orderId.invalidText
                  }
                </div>
              </div>
              <div className="form-group">
                <label for="phone">S in thoi{this.renderRequiredStart()}:</label>
                <input required value={this.props.phone} onChange={e => this.props.setPhone(e.target.value)} type="text" className={"form-control " + (this.state.phone.valid ? '' : "is-invalid")} id="phone" placeholder="S in thoi" onBlur={this.validatePhoneNumber} />
                <div className={"invalid-feedback " + (this.state.phone.valid ? '' : 'd-block')} >
                  {
                    validator.isEmpty(this.props.phone) ? this.state.phone.requiredTxt : this.state.phone.invalidText
                  }
                </div>
              </div>
              <Button
                text="Kim tra"
                icon="fas fa-shipping-fast"
                onClick={this.validateTrackingForm}
                buttonStyle={{
                  width: '100%',
                  backgroundColor: '#1f78d1',
                  paddingTop: '0.75vw',
                  paddingBottom: '0.75vw',
                  borderRadius: '0.5vw',
                  marginTop: '0.5vw',
                  marginBottom: '0.5vw'
                }}
                textStyle={{
                  color: '#FFFFFF',
                  fontWeight: 'bold'
                }}
                transform={5} //transform translateY negative in px
                shadowH={0} //horizontal shadow in px
                shadowV={15} //vertical shadow in px
                blur={20} //blur in px
                animationDuration={0.2} //animation duration in seconds
              />
            </form>
            {
              this.props.order && this.props.order.o_id && <form id="order-status-form" className="col-md-6 offset-md-1">
                {
                  this.props.order && this.props.order.o_id &&
                  <div className="form-group">
                    <OrderStatus order={this.props.order}></OrderStatus>
                  </div>
                }
              </form>
            }
          </div>
        </div>
      </div>
    );
  }
}
