import { connect } from 'react-redux'
import { MODEL_NAME } from '../model'
import { DEV_API_URL } from '../../../config/config'
import { setProducts } from '../action'
import axios from 'axios'
import ProductList from '../components/ProductList/ProductList'
import { addToCart, toggleLoading } from '../../../common/rootAction';

const mapStateToProps = (state) => ({
  products: state[MODEL_NAME].products,
  cart: state['ROOT_REDUCER'].cart
})

const mapDispatchToProps = (dispatch, props) => ({
  getData: async (c_name, l_name, s_name, page) => {
    dispatch(toggleLoading(true))
    let res = await axios.get(`${DEV_API_URL}/api/product/filter/page?c_name=${c_name}&l_name=${l_name}&s_name=${s_name}&page=${page}`)
    dispatch(toggleLoading(false))
    return dispatch(setProducts(res.data))
  },
  addToCart: (cart, item) => {
    if (cart.length == 0) {
      // Add First Item Into Cart
      return dispatch(addToCart([{
        item,
        quantity: 1
      }]))
    } else {
      // Flag newData To Check If item Is New In Cart Or Not
      let newData = true
      let newCart = cart.map((value, index) => {
        if(item._id == value.item._id) {
          // If item Exists In Cart Then Its Quantity Plus 1
          newData = false
          return {
            item: value.item,
            quantity: value.quantity + 1
          }
        } else {
          // Else Return Others Items In Cart
          return value
        }
      })
      if(newData) {
        // If item Is New Add New Item In newCart
        newCart = newCart.concat([{
          item: item,
          quantity: 1,
        }])
      }
      return dispatch(addToCart(newCart))
    }
  }
})

export default connect(mapStateToProps, mapDispatchToProps)(ProductList)