import { connect } from 'react-redux'
import { MODEL_NAME } from '../model'
import { setCategories, setProducts } from '../action'
import { DEV_API_URL } from '../../../config/config'
import axios from 'axios'
import Filter from '../components/Filter/Filter'

const mapStateToProps = (state) => ({
  categories: state[MODEL_NAME].categories
})

const mapDispatchToProps = (dispatch, props) => ({
  getData: async () => {
    let res = await axios.get(`${DEV_API_URL}/api/category/get/all`)
		return dispatch(setCategories(res.data))
  }
})

export default connect(mapStateToProps, mapDispatchToProps) (Filter)