import React from "react";
import ReactDOM from "react-dom";
import { Provider } from "react-redux";
import { persistor, store } from "./common/rootStore";
import { PersistGate } from "redux-persist/lib/integration/react";
import "./index.css";
import { BrowserRouter as Router, Route, withRouter } from "react-router-dom";

import Firebase, { FirebaseContext } from "./common/components/Firebase";

// import LoginPage from "./pages/LoginPage/index";
import HomePage from "./pages/HomePage/index";
import ProductListPage from "./pages/ProductListPage/index";
import ProductInfoPage from "./pages/ProductInfoPage/index";
import OrderPage from "./pages/OrderPage/index";
import TrackingPage from "./pages/TrackingPage/index";
import Footer from "./common/components/Footer/Footer";
import * as serviceWorker from "./serviceWorker";

ReactDOM.render(
  <Provider store={store}>
    <PersistGate persistor={persistor}>
      <div id="page-container">
        <div id="content-wrap">
          <FirebaseContext.Provider value={new Firebase()}>
            <Router>
              <Route exact path="/" component={HomePage} />
              <Route path="/products" component={ProductListPage} />
              <Route
                path="/product/:id"
                component={withRouter(ProductInfoPage)}
              />
              <Route path="/checkout" component={OrderPage} />
              <Route path="/tracking" component={TrackingPage} />
            </Router>
          </FirebaseContext.Provider>
        </div>
        <Footer />
      </div>
    </PersistGate>
  </Provider>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
