import { createStore, combineReducers } from "redux";
import { persistStore, persistReducer } from 'redux-persist'
import storage from 'redux-persist/lib/storage'

import autoMergeLevel2 from 'redux-persist/lib/stateReconciler/autoMergeLevel2'

//Models go here
import { MODEL_NAME as Login } from "../pages/LoginPage/model";
import { MODEL_NAME as Homepage } from "../pages/HomePage/model";
import { MODEL_NAME as ProductInfo } from "../pages/ProductInfoPage/model";
import { MODEL_NAME as ProductListPage } from "../pages/ProductListPage/model";
import { MODEL_NAME as OrderPage } from "../pages/OrderPage/model";
import { MODEL_NAME as DeliveryPage } from "../pages/DeliveryPage/model";
import { MODEL_NAME as TrackingPage } from "../pages/TrackingPage/model";

//Reducers go here
import login from "../pages/LoginPage/reducer";
import homepage from "../pages/HomePage/reducer";
import productinfo from "../pages/ProductInfoPage/reducer";
import ProductListPageReducer from "../pages/ProductListPage/reducer";
import orderpage from "../pages/OrderPage/reducer";
import deliverypage from "../pages/DeliveryPage/reducer";
import trackingpage from "../pages/TrackingPage/reducer";
import rootReducer from "./rootReducer";

const reducers = combineReducers({
  ROOT_REDUCER: rootReducer,
  [Login]: login,
  [Homepage]: homepage,
  [ProductInfo]: productinfo,
  [ProductListPage]: ProductListPageReducer,
  [OrderPage]: orderpage,
  [DeliveryPage]: deliverypage,
  [TrackingPage]: trackingpage
});

const persistConfig = {
  key: 'root',
  storage: storage,
  stateReconciler: autoMergeLevel2
 };

const pReducer = persistReducer(persistConfig, reducers)

export const store = createStore(pReducer);
export const persistor = persistStore(store)

