import React, { Component } from "react";
import Select from "react-select";
import validator from "validator";
import "./DeliveryForm.css";
import Button from "../../../../common/components/Button/Button";
// import { throwStatement } from "@babel/types";

export default class DeliveryForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cityId: null,
      districtId: null,
      wardId: null,
      u_name: {
        value: "",
        valid: true,
        required: true,
        requiredTxt: "Vui lng nhp h tn"
      },
      phone: {
        value: "",
        valid: true,
        required: true,
        invalidText: "S in thoi khng hp l.",
        requiredTxt: "Vui lng nhp s in thoi."
      },
      email: {
        value: "",
        valid: true,
        required: false,
        invalidText: "Email khng hp l."
      },
      fbLink: {
        value: "",
        valid: true,
        required: false,
        invalidText: "Facebook khng hp l."
      },
      address: {
        city: {
          value: "",
          valid: true,
          required: true,
          requiredTxt: "Vui lng chn Tnh/Thnh ph"
        },
        district: {
          value: "",
          valid: true,
          required: true,
          requiredTxt: "Vui lng chn Qun/Huyn"
        },
        ward: {
          value: "",
          valid: true,
          required: true,
          requiredTxt: "Vui lng chn Phng/X"
        },
        street: {
          value: "",
          valid: true,
          required: true,
          requiredTxt: "Vui lng nhp a ch"
        }
      }
    };
  }

  componentDidMount = () => {
    this.props.getCityList();
  };

  changeCity = async optionSelected => {
    const cityId = optionSelected.value;
    await this.setState(
      {
        cityId: cityId,
        districtId: null,
        wardId: null,
        address: {
          ...this.state.address,
          city: {
            ...this.state.address.city,
            value: this.props.cityList.find(city =>
              validator.equals(city.value, cityId)
            ).label,
            valid: true
          },
          district: {
            ...this.state.address.district,
            value: "",
            valid: false
          },
          ward: {
            ...this.state.address.ward,
            value: "",
            valid: false
          }
        }
      },
      this.validateCity
    );
    this.props.getDistrictList(cityId);
  };

  changeDistrict = districtId => {
    this.setState(
      {
        districtId: districtId,
        wardId: null,
        address: {
          ...this.state.address,
          district: {
            ...this.state.address.district,
            value: districtId.label,
            valid: true
          },
          ward: {
            ...this.state.address.ward,
            value: "",
            valid: false
          }
        }
      },
      this.validateDistrict
    );
    this.props.getWardList(this.state.cityId, districtId.value);
  };

  changeWard = wardId => {
    this.setState(
      {
        wardId: wardId,
        address: {
          ...this.state.address,
          ward: {
            ...this.state.address.ward,
            value: wardId.label,
            valid: true
          }
        }
      },
      this.validateWard
    );
  };

  renderRequiredStart() {
    return <span style={{ color: "red" }}>*</span>;
  }

  validateUserName = e => {
    const u_name = this.state.u_name.value;
    this.setState({
      u_name: { ...this.state.u_name, valid: !validator.isEmpty(u_name) }
    });
  };

  validatePhoneNumber = e => {
    const phone = this.state.phone.value;
    this.setState({
      phone: {
        ...this.state.phone,
        valid: validator.isMobilePhone(phone, "vi-VN")
      }
    });
  };

  validateEmail = e => {
    const email = this.state.email.value;
    if (email) {
      this.setState({
        email: { ...this.state.email, valid: validator.isEmail(email) }
      });
    }
  };

  validateFacebookLink = e => {
    const fbLink = this.state.fbLink.value;
    if (fbLink) {
      this.setState({
        fbLink: {
          ...this.state.fbLink,
          valid: validator.isURL(fbLink, {
            host_whitelist: [/^.*facebook\.com$/]
          })
        }
      });
    }
  };

  validateCity = e => {
    const city = this.state.address.city.value;
    this.setState({
      address: {
        ...this.state.address,
        city: {
          ...this.state.address.city,
          valid: !validator.isEmpty(city)
        }
      }
    });
  };

  validateDistrict = e => {
    const district = this.state.address.district.value;
    this.setState({
      address: {
        ...this.state.address,
        district: {
          ...this.state.address.district,
          valid: !validator.isEmpty(district)
        }
      }
    });
  };

  validateWard = e => {
    const ward = this.state.address.ward.value;
    this.setState({
      address: {
        ...this.state.address,
        ward: {
          ...this.state.address.ward,
          valid: !validator.isEmpty(ward)
        }
      }
    });
  };

  validateStreet = e => {
    const street = this.state.address.street.value;
    this.setState({
      address: {
        ...this.state.address,
        street: {
          ...this.state.address.street,
          valid: !validator.isEmpty(street)
        }
      }
    });
  };

  submitOrder = async () => {
    await this.validateUserName();
    await this.validatePhoneNumber();
    await this.validateEmail();
    await this.validateFacebookLink();
    await this.validateCity();
    await this.validateDistrict();
    await this.validateWard();
    await this.validateStreet();
    if (
      this.state.u_name.valid &&
      this.state.phone.valid &&
      this.state.email.valid &&
      this.state.phone.valid &&
      this.state.address.city.valid &&
      this.state.address.district.valid &&
      this.state.address.ward.valid &&
      this.state.address.street.valid
    ) {
      return this.props.createOrder(this.props.cart, this.state);
    }
  };

  render() {
    const { cityId, districtId, wardId } = this.state;
    return (
      <div className="container col-md-9 mb-3">
        <h3 className="page-title">a ch nhn hng</h3>
        <form id="delivery-form">
          <div className="form-group row">
            <label for="u_name" className="col-sm-2 col-form-label">
              H tn{this.renderRequiredStart()}:
            </label>
            <div className="col-sm-10">
              <input
                type="text"
                className="form-control"
                id="u_name"
                placeholder="H tn"
                onBlur={this.validateUserName}
                value={this.state.u_name.value}
                onChange={e => {
                  this.setState({
                    u_name: { ...this.state.u_name, value: e.target.value }
                  });
                }}
              />
              <div
                className={
                  "invalid-feedback " +
                  (this.state.u_name.valid ? "" : "d-block")
                }
              >
                {this.state.u_name.requiredTxt}
              </div>
            </div>
          </div>

          <div className="form-group row">
            <label for="phone" className="col-sm-2 col-form-label">
              S in thoi{this.renderRequiredStart()}:
            </label>
            <div className="col-sm-10">
              <input
                type="text"
                className="form-control"
                id="phone"
                placeholder="S in thoi"
                onBlur={this.validatePhoneNumber}
                value={this.state.phone.value}
                onChange={e => {
                  this.setState({
                    phone: { ...this.state.phone, value: e.target.value }
                  });
                }}
              />
              <div
                className={
                  "invalid-feedback " +
                  (this.state.phone.valid ? "" : "d-block")
                }
              >
                {this.state.phone.value
                  ? this.state.phone.invalidText
                  : this.state.phone.requiredTxt}
              </div>
            </div>
          </div>

          <div className="form-group row">
            <label for="email" className="col-sm-2 col-form-label">
              Email:
            </label>
            <div className="col-sm-10">
              <input
                type="email"
                className="form-control"
                id="email"
                placeholder="Email"
                onBlur={this.validateEmail}
                value={this.state.email.value}
                onChange={e => {
                  this.setState({
                    email: { ...this.state.email, value: e.target.value }
                  });
                }}
              />
              <div
                className={
                  "invalid-feedback " +
                  (this.state.email.valid ? "" : "d-block")
                }
              >
                {this.state.email.invalidText}
              </div>
            </div>
          </div>

          <div className="form-group row">
            <label for="email" className="col-sm-2 col-form-label">
              Facebook:
            </label>
            <div className="col-sm-10">
              <input
                type="text"
                className="form-control"
                id="fbLink"
                placeholder="Facebook"
                onBlur={this.validateFacebookLink}
                value={this.state.fbLink.value}
                onChange={e => {
                  this.setState({
                    fbLink: { ...this.state.fbLink, value: e.target.value }
                  });
                }}
              />
              <div
                className={
                  "invalid-feedback " +
                  (this.state.fbLink.valid ? "" : "d-block")
                }
              >
                {this.state.fbLink.invalidText}
              </div>
            </div>
          </div>

          <div className="form-group row">
            <label for="province" className="col-sm-2 col-form-label">
              Tnh/Thnh ph{this.renderRequiredStart()}:
            </label>
            <div className="col-sm-10">
              <Select
                id="province"
                onChange={this.changeCity}
                options={this.props.cityList}
                placeholder={"Tnh/Thnh ph"}
                // onBlur={this.validateCity}
              />
              <div
                className={
                  "invalid-feedback " +
                  (this.state.address.city.valid ? "" : "d-block")
                }
              >
                {this.state.address.city.requiredTxt}
              </div>
            </div>
          </div>

          <div className="form-group row">
            <label for="district" className="col-sm-2 col-form-label">
              Qun/Huyn{this.renderRequiredStart()}:
            </label>
            <div className="col-sm-10">
              <Select
                isDisabled={!cityId}
                value={districtId}
                id="district"
                onChange={this.changeDistrict}
                options={this.props.districtList}
                placeholder={"Qun/Huyn"}
                // onBlur={this.validateDistrict}
              />
              <div
                className={
                  "invalid-feedback " +
                  (this.state.address.district.valid ? "" : "d-block")
                }
              >
                {this.state.address.district.requiredTxt}
              </div>
            </div>
          </div>

          <div className="form-group row">
            <label for="ward" className="col-sm-2 col-form-label">
              Phng/X{this.renderRequiredStart()}:
            </label>
            <div className="col-sm-10">
              <Select
                isDisabled={!cityId || !districtId}
                value={wardId}
                id="ward"
                onChange={this.changeWard}
                options={this.props.wardList}
                placeholder={"Phng/X"}
                // onBlur={this.validateWard}
              />
              <div
                className={
                  "invalid-feedback " +
                  (this.state.address.ward.valid ? "" : "d-block")
                }
              >
                {this.state.address.ward.requiredTxt}
              </div>
            </div>
          </div>

          <div className="form-group row">
            <label for="address" className="col-sm-2 col-form-label">
              a ch{this.renderRequiredStart()}:
            </label>
            <div className="col-sm-10">
              <textarea
                className="form-control"
                id="address"
                rows="3"
                placeholder="a ch"
                onBlur={this.validateStreet}
                value={this.state.address.street.value}
                onChange={e => {
                  this.setState({
                    address: {
                      ...this.state.address,
                      street: {
                        ...this.state.address.street,
                        value: e.target.value
                      }
                    }
                  });
                }}
              />
              <div
                className={
                  "invalid-feedback " +
                  (this.state.address.street.valid ? "" : "d-block")
                }
              >
                {this.state.address.street.requiredTxt}
              </div>
            </div>
          </div>

          <div className="row">
            <div className="col-lg-12 d-flex flex-row justify-content-between">
              <div
                style={{ backgroundColor: "#f57f17" }}
                className="btn order-btn product-info-txt"
                onClick={() => {
                  window.location.reload();
                }}
              >
                Tr v
              </div>

              <div
                className="btn order-btn product-info-txt"
                style={{ backgroundColor: "rgb(31, 120, 209)" }}
                onClick={this.submitOrder}
              >
                Mua ngay
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}
