import { connect } from "react-redux";
import { MODEL_NAME } from "../model";
import { setOrder, setPhone, setOrderId } from "../action";
import Tracking from "../components/Tracking/Tracking";
import axios from "axios";
import { DEV_API_URL } from "../../../config/config";
import { toggleLoading } from "../../../common/rootAction";

const mapStateToProps = state => ({
  orderId: state[MODEL_NAME].orderId,
  phone: state[MODEL_NAME].phone,
  order: state[MODEL_NAME].order
});

const mapDispatchToProps = (dispatch, props) => ({
  getOrder: async (orderId, phone) => {
    try {
      dispatch(toggleLoading(true))
      orderId = orderId.replace('#', '')
      let res = await axios.get(`${DEV_API_URL}/api/order?o_id=${orderId}&phone=${phone}`);
      dispatch(setOrder(res.data));
      dispatch(toggleLoading(false))
    } catch (error) {
      dispatch(setOrder({}));
      dispatch(setPhone(''))
      dispatch(setOrderId(''))
      dispatch(toggleLoading(false))
      alert('Khng tm thy order')
    }
  },
  setPhone: async (phone) => {
    return dispatch(setPhone(phone))
  },
  setOrderId: async (orderId) => {
    return dispatch(setOrderId(orderId))
  },
  setOrder: async (order) => {
    return dispatch(setOrder(order));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Tracking);
